@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  background-image:url("/images/background_tropical1.jpg");
  background-size: cover;
  height: 100vh;
  /* vh is there so the size of the app is the same than the window/body size */
}


.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.avatar {
    width: 100%;
    height: 100%;
    display: flex; 
    /* justify-content: center; */
    align-items: center;
  }
  
  .avatar img {
    display: block;
    border-radius: 50%;
    width: 50%;
    height: 50%;
    object-fit: cover;
    border: white solid;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  }
.card {
    position: relative;
    margin: 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 15px;
    padding: 1rem;
    overflow: hidden;
    background:rgba(255, 255, 255, 0.9);
    border: white solid 3px;
}
  
.user-item {
  margin: 1rem;
  width: calc(45% - 2rem);
  min-width: 17.5rem;
}

.user-item a {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: white;
  background-color: rgba(88,112,46,0.8);
}

.user-item a:hover,
.user-item a:active {
  background-color: rgba(88, 112, 46, 0.582);
}

.user-item__content {
  padding: 0;
}

.user-item__image {
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

.user-item__info h2 {
  font-size: 1rem;
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  color: white;
  font-size: 23px;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
  color: #292929;
}

.user-item__info h3 {
  margin: 0;
  font-size: 20px;
}
.users-list {
    list-style: none;
    margin: 0 auto;
    padding: 0;
    width: 50%;
    max-width: 50rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
  }
  
.modal {
    z-index: 100;
    position: fixed;
    top: 22vh;
    left: 10%;
    width: 80%;
    background:rgba(255, 255, 255, 0.8);
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
    border-radius: 8px;
    border: white solid 3px;

  }
  
  .modal__header {
    width: 100%;
    padding: 1rem 0.5rem;
    background:rgba(88,112,46,0.8);
    color: white;
    border: white solid 3px;

  }
  
  
  .modal__header h2 {
    margin: 0.1rem;
  }
  
  .modal__content {
    padding: 1rem 0.5rem;
  }
  
  .modal__footer {
    padding: 1rem 0.5rem;
  }
  
  @media (min-width: 768px) {
    .modal {
      left: calc(50% - 20rem);
      width: 40rem;
    }
  }
  
  .modal-enter {
    transform: translateY(-10rem);
    opacity: 0;
  }
  
  .modal-enter-active {
    transform: translateY(0);
    opacity: 1;
    transition: all 200ms;
  }
  
  .modal-exit {
    transform: translateY(0);
    opacity: 1;
  }
  
  .modal-exit-active {
    transform: translateY(-10rem);
    opacity: 0;
    transition: all 200ms;
  }
  
.button {
    font: inherit;
    padding: 0.5rem 1.5rem;
    border: 1px solid #58702e;
    border-radius: 4px;
    background: #58702e;
    color: white;
    cursor: pointer;
    margin-right: 1rem;
    text-decoration: none;
    display: inline-block;
    font-family: 'Montserrat', sans-serif;

  }
  
  .button:focus {
    outline: none;
  }
  
  .button:hover,
  .button:active {
    background: #58702e8c;
    border-color: #58702e;
  }
  
  .button--inverse {
    background: transparent;
    color: #58702ea9;
  }
  
  .button--inverse:hover,
  .button--inverse:active {
    color: white;
    background: #58702ec9;
  }
  
  .button--danger {
    background: #58702e;
    border-color: #58702e;
  }

 
  
  .button--danger:hover,
  .button--danger:active {
    background: #58702ec5;
    border-color: #58702e;
  }
  
  .button:disabled,
  .button:hover:disabled,
  .button:active:disabled {
    background: #ccc;
    color: #979797;
    border-color: #ccc;
    cursor: not-allowed;
  }
  
  .button--small {
    font-size: 0.8rem;
  }
  
  .button--big {
    font-size: 1.5rem;
  }

.lds-dual-ring {
    display: inline-block;
    width: 64px;
    height: 64px;
  }
  
  .lds-dual-ring:after {
    content: ' ';
    display: block;
    width: 46px;
    height: 46px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid #046638;
    border-color: #046638 transparent #046638 transparent;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite;
  }
  
  .loading-spinner__overlay {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  @-webkit-keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
.form-control {
    margin: 1rem 0;
  }
  
  .form-control label,
  .form-control input,
  .form-control textarea {
    display: block;
  }
  
  .form-control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    font-family: 'Lora', serif;
    font-size: 17px;
  }
  
  .form-control input,
  .form-control textarea {
    width: 100%;
    font: inherit;
    border: 1px solid #ccc;
    background: #f8f8f8;
    padding: 0.15rem 0.25rem;
  }
  
  .form-control input:focus,
  .form-control textarea:focus {
    outline: none;
    background: #ebebeb;
    border-color: #510077;
  }
  
  .form-control--invalid label,
  .form-control--invalid p {
    color: red;
  }
  
  .form-control--invalid input,
  .form-control--invalid textarea {
    border-color: red;
    background: #ffd1d1;
  }
.image-upload.center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .image-upload__preview {
    width: 13rem;
    height: 13rem;
    border: 1px solid #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 1rem;
  }
  
  .image-upload__preview img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
.event-form {
    list-style: none;
    margin: 0 auto;
    padding: 1rem;
    width: 90%;
    max-width: 40rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 6px;
    background:rgba(255, 255, 255, 0.6);
    border: white solid 3px;
  }
  
/* google map style here */
.map {
    width: 100%;
    height: 100%;
  }
  
.event-item {
    margin: 1rem 0;
  }
  
  .event-item__content {
    padding: 0;
  }
  
  .event-item__info {
    padding: 1rem;
    text-align: center;
  }
  
  .event-item__image {
    width: 100%;
    height: 12.5rem;
    margin-right: 1.5rem;
  }
  
  .event-item__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .event-item__info h2,
  .event-item__info h3,
  .event-item__info p {
    margin: 0 0 0.5rem 0;
  }
  
  .event-item__actions {
    padding: 1rem;
    text-align: center;
    border-top: 1px solid #ccc;
  }
  
  .event-item__modal-content {
    padding: 0;
  }
  
  .event-item__modal-actions {
    text-align: right;
  }
  
  .event-item__actions button,
  .event-item__actions a {
    margin: 0.5rem;
  }
  
  /* map size */
  .map-container {
    height: 11rem;
    width: 100%;
  }
  

  @media (min-width: 768px) {
    .event-item__image {
      height: 20rem;
    } 
  }
.event-list {
    list-style: none;
    margin: 1rem auto;
    padding: 0;
    width: 90%;
    max-width: 40rem;
    margin-top: 40px;
  }
  
.authentication__header {
    color: white;
    text-align: center;
  }
  
  .authentication {
    width: 90%;
    max-width: 25rem;
    margin: 7rem auto;
    text-align: center;
    background: rgba(255, 255, 255, 0.6);

  }
  
  .authentication form {
    margin-bottom: 1rem;
  }
.main-header {
  width: 100%;
  align-items: center;
  justify-content: center;
  /* background-image: linear-gradient(to top, #bfe7f8 0%, #3498db 100%); */
  background-color: rgba(251, 215, 227, 0.6);
  /* box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26); */
  padding: 0 1rem;
  z-index: 5;
}

main {
  margin-top: 0;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}
.nav-links {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .nav-links li {
    margin: 1rem;
  }

  .nav-links ul {
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255, 0.2) 75%, rgba(255, 255, 255, 0) 100%);
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.1), inset 0 0 1px rgba(255, 255, 255, 0.6);
  }
  
  .nav-links a {
    padding: 15px;
    font-family: "Open Sans";
    text-transform:uppercase;
    font-size: 20px;
    text-decoration: none;
    display: block;
    /* color: white; */
    /* background-color: rgba(251, 215, 227, 0.9); */
    color: rgba(247, 39, 108, 0.9);
    text-shadow: 2px 2px 3px rgba(255,255,255,0.2);
    background: #f05588;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;

  }
  
  .nav-links a:hover,
  .nav-links a:active,
  .nav-links a.active {
    text-shadow: 2px 2px 3px rgba(255,255,255,0.2);
    border: #f8c1d4 solid 3px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
  }
  
  .nav-links button {
    cursor: pointer;
    border: 1px solid #f8c1d4;
    color: #f05588;
    background: transparent;
    padding: 0.5rem;
    font: inherit;
  }
  
